.home{

	width: 100;
}



.home h1{
	opacity: 0;
	font-size: 40px;
	-webkit-animation: fadein 1.5s;
	        animation: fadein 1.5s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-delay: 0.4s;
	        animation-delay: 0.4s;
	margin-left: 5%;
	margin-top: 5%;
}
.home h3{

	font-size: 30px;
	margin-left: 11%;
	opacity: 0;
	-webkit-animation: fadein;
	        animation: fadein;
	-webkit-animation-delay: 1s;
	        animation-delay: 1s;
	-webkit-animation-duration: 1.5s;
	        animation-duration: 1.5s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;

	
}

.App-logo {

	
	-webkit-animation:App-logo-spin infinite 5s linear normal;

	
	        animation:App-logo-spin infinite 5s linear normal;
	height: 200px;
	overflow: hidden;
	-webkit-animation-play-state: running;
	        animation-play-state: running;
	
}
.logo{
	margin-left: 30%;
	margin-right: 30%;
	opacity: 0;
	-webkit-animation:applogo-pop 2s 1.5s forwards;
	        animation:applogo-pop 2s 1.5s forwards;
}




@-webkit-keyframes fadein{

	from {opacity: 0;}
	to {opacity: 1;}
}




@keyframes fadein{

	from {opacity: 0;}
	to {opacity: 1;}
}

@-webkit-keyframes applogo-pop{

	from{-webkit-transform: scale(0);transform: scale(0);}
	to{-webkit-transform: scale(1);transform: scale(1);opacity: 1;}
}

@keyframes applogo-pop{

	from{-webkit-transform: scale(0);transform: scale(0);}
	to{-webkit-transform: scale(1);transform: scale(1);opacity: 1;}
}

@-webkit-keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




